form,
.form {
  *,
  *:before,
  *:after {
    box-sizing: border-box;
  }

  .field {
    &.radio {
      display: flex;
    }

    + .field {
      padding-top: 10px;
    }

    &.buttons {
      border-top: 1px solid #eaeaea;
      margin-top: 20px;
      padding-top: 20px;
    }
  }

  label,
  .label {
    display: block;
    font-size: 16px;
    padding-bottom: 4px;

    .label-title {
      font-weight: 700;
      margin: 0 0 10px;
    }

    .label-description {
      margin: 0 0 10px;
    }
  }

  input[type='radio'] {
    height: 30px;
    margin-right: 10px;
    width: 30px;
  }

  input[type='password'],
  input[type='text'] {
    appearance: none;
    border: 2px solid #ddd;
    border-radius: 2px;
    color: #333;
    display: inline-block;
    font-family: inherit;
    font-size: 16px;
    height: 40px;
    padding: 8px;
    width: 100%;

    &:disabled {
      background: #ccc;
    }
  }

  textarea {
    appearance: none;
    border: 2px solid #ddd;
    border-radius: 2px;
    color: #333;
    display: inline-block;
    font-family: inherit;
    font-size: 16px;
    padding: 8px;
    width: 100%;

    &[readonly] {
      background: #eee;
    }
  }

  .button {
    align-items: center;
    background: #2163ef;
    border: 0;
    border-radius: 2px;
    color: #fff;
    cursor: pointer;
    display: inline-block;
    font-family: inherit;
    font-size: 16px;
    font-weight: 500;
    height: 44px;
    justify-content: center;
    min-width: 8ch;
    padding: 0 8px;

    &:disabled {
      cursor: default;
      opacity: 0.7;
    }

    &.is-link {
      background: none;
      color: #2163ef;
    }

    &.is-secondary {
      background: #ccc;
      color: #222;
    }

    + .button {
      margin-left: 10px;
    }
  }

  .checkbox,
  .radio {
    label {
      display: inline-block;
      height: 30px;
      line-height: 30px;
      padding: 0;
      vertical-align: middle;
    }

    input[type='checkbox'],
    input[type='radio'] {
      border: 2px solid #ddd;
      border-radius: 2px;
      display: inline-block;
      height: 30px;
      margin: 0 10px 0 0;
      position: relative;
      top: -2px;
      vertical-align: middle;
      width: 30px;
    }
  }
}
