@import 'graphiql/graphiql.min.css';
@import 'graphiql-code-exporter/CodeExporter.css';

.graphiql-container {
  .error-or-loading {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100vh;
    width: 100vw;

    .logo {
      width: 300px;
    }
  }

  .graphiql-explorer-actions {
    overflow: hidden;
  }

  .graphiql-explorer-root > div > div {
    padding-left: 3px;
  }

  .title {
    width: 100px;

    img {
      display: block;
    }
  }

  .toolbar {
    width: 100%;
  }

  .endpoint {
    background: #eee;
    color: #000;
    font-variant: small-caps;
    font-weight: 700;
    height: 15px;
    letter-spacing: 1px;
    line-height: 14px;
    overflow: hidden;
    padding: 6px 0 8px 13px;
    text-overflow: ellipsis;
    text-transform: lowercase;
    white-space: nowrap;
    width: 100%;
  }

  .history-contents {
    button[aria-label] {
      font-size: 20px;

      &.favorited {
        display: block;
      }
    }
  }

  .docExplorerWrap {
    z-index: 15 !important;
  }

  .graphiql-operation-title-bar {
    button {
      height: 20px !important;
      margin-left: 5px !important;
      vertical-align: middle;
      width: 20px !important;
    }

    input {
      font-family: Consolas, Inconsolata, 'Droid Sans Mono', Monaco, monospace;
    }
  }

  .graphiql-explorer-actions {
    select {
      margin-left: 5px;
      margin-right: 5px;
    }
  }
}
