body {
  margin: 0;
  height: 100vh;
  overflow: hidden;
}

#root {
  height: 100vh;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0 0 20px;
  padding: 0;

  &:last-child {
    margin-bottom: 0;
  }
}

hr {
  border: 0;
  border-bottom: 1px solid #eaeaea;
  margin: 0 0 10px;
  padding: 5px 0;
}

pre {
  white-space: pre-wrap;
}

.sr-only {
  border-width: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
