.server-status-overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba($color: #000000, $alpha: 0.3);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.server-status-pop {
  background-color: white;
  width: 200px;
  height: 50px;
  padding: 10px;
  text-align: center;
  padding: 1rem 1rem 0rem 1rem;
  border-radius: 5px;
  -webkit-box-shadow: 0 3px 7px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0 3px 7px rgba(0, 0, 0, 0.3);
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.3);
}

.server-status-pop .pop-copy {
  line-height: 21px;
}

.server-status-pop code {
  border-radius: 2px;
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  padding: 1px;
  border: 1px solid #00b2ca;
  background: rgba(0, 178, 202, 0.3);
  color: #193460;
}

.server-status-pop .pop-icon {
  margin-bottom: 0;
  padding: 0;
  font-size: 28px;
}
