.generator-categories {
  border-bottom: 1px solid #eaeaea;
  margin: 0 0 10px;

  .list {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;

    .list-item {
      display: inline-block;

      + li {
        margin: 0 0 10px 10px;
        margin-top: 0;
      }

      [type='radio'] {
        display: none;
        height: auto;
        position: absolute;
      }

      p {
        font-size: 14px;
      }

      label {
        border: 1px solid #ccc;
        border-radius: 10px;
        cursor: pointer;
        font-size: 14px;
        padding: 6px;

        &:hover {
          background: #ccc;
        }
      }

      :checked + label {
        background: #efefef;
      }
    }
  }
}
