.generator-wrap {
  background: #fff;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.15);
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  position: relative;
  width: 350px;
  z-index: 20;

  &.is-generators {
    width: 450px;
  }
}

.generator-title-bar {
  cursor: default;
  display: flex;
  height: 34px;
  line-height: 14px;
  padding: 8px 8px 5px;
  position: relative;
  -webkit-user-select: none;
  user-select: none;

  .generator-title {
    flex: 1 1;
    font-weight: 700;
    overflow-x: hidden;
    padding: 10px 0 10px 10px;
    text-align: center;
    text-overflow: ellipsis;
    user-select: text;
    white-space: nowrap;
  }

  .generator-close {
    cursor: pointer;
    font-size: 18px;
    margin: -7px -8px -6px 0;
    padding: 18px 16px 15px 12px;
    background: 0;
    border: 0;
    line-height: 14px;
  }
}

.generator-content {
  background-color: #fff;
  border-top: 1px solid #d6d6d6;
  bottom: 0;
  flex: 1 1;
  left: 0;
  overflow-y: auto;
  padding: 20px 15px;
  position: absolute;
  right: 0;
  top: 47px;
}

.generator-errors {
  background-color: #ff3860;
  color: #fff;
  font-size: 16px;
  line-height: 1.6;
  margin: 0 0 20px;
  padding: 20px;

  ul,
  li {
    list-style: none;
    margin: 0;
    padding: 0;
  }
}

.has-footer {
  .generator-content {
    border-bottom: 1px solid #d6d6d6;
    bottom: 47px;
  }

  .generator-footer-bar {
    bottom: 0;
    display: flex;
    height: 47px;
    left: 0;
    padding: 0 30px;
    position: absolute;
    right: 0;
    user-select: none;
  }
}

.generator-view-switcher {
  align-items: center;
  display: flex;
  justify-content: flex-end;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;

  .generator-view-switcher-switch {
    appearance: none;
    background: none;
    border: 1px solid #ccc;
    border-radius: 10px;
    cursor: pointer;
    font-size: 14px;
    height: 30px;
    line-height: 16px;
    padding: 6px;

    &.is-selected {
      background: #efefef;
    }

    &:hover {
      background: #ccc;
    }

    span {
      margin-left: 6px;
    }
  }

  .generator-view-switcher-view {
    margin: 0 0 0 20px;
    padding: 0;
  }
}
