.catalogue-card {
  align-items: stretch;
  border: 0;
  border-radius: 10px;
  cursor: pointer;
  display: flex;
  padding: 10px;
  width: 100%;

  &:disabled {
    cursor: default;
  }

  &:not(:disabled) {
    &:focus,
    &:hover {
      background: #ddd;
    }
  }

  .catalogue-card-image {
    align-items: center;
    border-right: 1px solid #fff;
    display: flex;
    flex: 0 0 auto;
    justify-content: center;
    min-height: 50px;
    margin-right: 10px;
    padding-right: 10px;
    width: 110px;

    img {
      max-height: 40px;
      max-width: 100px;
    }
  }

  .catalogue-card-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: left;
    width: 100%;

    .catalogue-card-title {
      margin: 0 0 10px;
      padding: 0;
    }

    .catalogue-card-text {
      margin: 0;
      padding: 0;
    }
  }

  &.is-grid {
    .catalogue-card-image {
      border-right: 0;
      display: flex;
      margin: 0;
      padding: 0;
      width: 100%;
    }

    .catalogue-card-content {
      display: none;
    }
  }
}

.catalogue {
  * {
    box-sizing: border-box;
  }

  .catalogue-item-list {
    list-style: none;
    margin: 0;
    padding: 0;

    .catalogue-item {
      margin: 0;
      padding: 0;

      + .catalogue-item {
        margin-top: 15px;
      }
    }
  }

  &.is-grid {
    .catalogue-item-list {
      display: flex;
      flex-wrap: wrap;
    }

    .catalogue-item,
    .catalogue-item + .catalogue-item {
      margin: 0 0 10px;
      width: calc(33.3% - (20px / 3));
    }

    .catalogue-item:nth-child(3n - 1),
    .catalogue-item:nth-child(3n + 0) {
      margin-left: 10px;
    }
  }
}
